.footer__container {
  padding: 5.6rem 1.4rem;
  @include grid(grid, repeat(3, 1fr), 0);
}

.date-tem:last-child {
  @extend %transition;
  &:hover {
    @extend %hover-color;
  }
}

.date-item:not(:last-child) {
  margin-right: 1.5rem;
}

.footer-logo {
  @extend %transition;
  &:hover {
    @extend %hover-color;
  }
  justify-self: center;
}

.footer-social {
  justify-self: end;
  padding-top: 0.5rem;
}

.social-list {
  display: inline-flex;
}
.social-list__item {
  &:not(:last-child) {
    margin-right: 2rem;
  }
}
