.price-items {
  @include grid(grid, repeat(3, 1fr), 2.5rem);
  padding-bottom: 10rem;
}

.price-item {
  padding: 4.2rem 0 4rem 0;
  @extend %text-center;
  border: 2px solid $main-color;
}

.price-item__price {
  padding-top: 2rem;
}
.price-description {
  padding: 2rem 0 4rem;
  font-size: $fs-text;
  line-height: $lh-text;
}

.price-description__item {
  display: block;
}

.price-button {
  padding-inline: 3.9rem;
}
