.contact-form {
  padding-top: 4rem;
}

.contact-form__input,
.contact-form__textarea {
  width: 100%;
  padding: 0.45rem 0 0.3rem 1.8rem;
  line-height: $lh-main;
  color: $main-color;
  background: rgba(0, 0, 0, 0.5);
  border: 2px solid $main-color;
  &:focus {
    outline: none;
  }
}
.contact-form__textarea {
  min-height: 10rem;
  margin-bottom: 2.2rem;
  padding-top: 0.2rem;
}

.contact-form__input {
  margin-bottom: 2rem;
}
