.portfolio-buttons {
  @extend %flex-main;
  @extend %align-center;
  @extend %justify-center;
  gap: 0.4rem;
  padding-top: 0.08rem;
}
.portfolio-button {
  &--active {
    background-color: $main-color;
    color: $light-color;
  }
}

.portfolio-button:nth-child(1) {
  padding-inline: 7.8rem;
}

.portfolio-button:nth-child(2) {
  padding-inline: 7.8rem;
}

.portfolio-button:nth-child(3) {
  padding-inline: 7rem;
  padding-top: 0.8rem;
}

.portfolio-button:nth-child(4) {
  padding-inline: 7.25rem;
}

.portfolio-items {
  @include grid(grid, repeat(3, 1fr), 2.5rem);
  padding-top: 5.9rem;
}
